html{
  scroll-snap-type: y mandatory;
}

.App{background-color: black;
height: 100vh;
display: grid;
place-items: center;
}

.app__videos{
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 500px;
  border: 1px solid black;
  border-radius: 20px;
  overflow: scroll;
  scrollbar-width: nome;
  scroll-snap-type: y mandatory;
  position: relative;
}

/* Esconde a scroll bar no chrome, safari e opera */

.app__videos::-webkit-scrollbar{
  display: none;
}

/* Esconder a scrollbar no internet explorer, edge e firefox */

.app__videos{
  -ms-overflow-style: none;
  scrollbar-width: none;
}

@media only screen and (max-width: 440px){
  .app__videos{
    width: 100%;
    height: 100vh;
  }
}